@use "../" as *;

main {
  display: grid;
  grid-template-rows: auto auto;
  grid-template-columns: min(100%, 80ch) 1fr;
  gap: 0 max(1rem, 3vw);
  grid-template-areas:
    "masthead ."
    "content toc";
  padding: 10vh 10%;
  padding: max(48px, 10vh) max(64px, 10%);
  will-change: transform, width;
  transition:
    width animation("expressive"),
    transform animation("expressive");

  .site-sidebar-header[hidden] ~ &,
  .site-sidebar-header[data-animation-state="closing"] ~ & {
    width: 100%;
    transform: translateX(0);
  }

  &,
  .site-sidebar-header[data-animation-state="opening"] ~ & {
    transform: translateX(300px);
    
    @include breakpoint-above("site-header") {
      width: calc(100% - 300px);
    }
  }

  @include breakpoint-below("site-header") {
    width: 100%;
  }
}

.page-inner-sidebar {
  grid-area: toc;
}

.page-article {
  grid-area: content;
}

.page-masthead {
  grid-area: masthead;
  background-color: color("gray", "0");
  margin: calc(max(48px, 10vh) * -1) -50vw 3rem;
  padding: max(48px, 10vh) 50vw 3rem;
}

.page-category {
  font-size: font-size("small");
  letter-spacing: .1em;
  margin: 0 0 space("tight") !important;
  text-transform: uppercase;
  color: theme-color("primary");

  @include breakpoint-below("side-nav") {
    display: none;
  }
}

.lead {
  @include font-size("medium-plus", $line-height: false);

  color: color("gray", "300");
  font-weight: 300;
  letter-spacing: 0.005em;
  line-height: line-height("loose");
}

:is(h2, h3, h4, h5, h6)[data-anchored] {
  --heading-anchor-color: transparent;

  display: grid;
  grid-template-columns: 1.5ch auto;
  position: relative;
  left: -1.5ch;

  &:hover {
    --heading-anchor-color: #{color("gray")};
  }

  .heading-anchor {
    color: var(--heading-anchor-color);
    text-decoration: none;
    transform: scale(.7);
    transition: color animation("productive");

    &:hover,
    &:focus {
      --heading-anchor-color: #{color("gray", "500")};
    }
  }
}