@use "../" as *;

code,
pre {
  font-family: font-stack("monospace");

  &:not([class*="language-"]) {
    background: color("gray", "0");
    border: 1px solid color("shade", "0");
  }
}

h1, .h1 {
  font-weight: font-weight("semibold");
}

h2, h3, h4,
.h2, .h3, .h4 {
  font-weight: font-weight("normal");
}